export default {
  checkUser() {
    let user = localStorage.getItem("evandro.user")
    if (!user || user === '') {
      return false
    }
    return true
  },

  login(username: string, password: string) {
    if (username != 'evandro') {
      return false
    }
    if (password != 'evandro') {
      return false
    }

    localStorage.setItem("evandro.user", username)
    localStorage.setItem("evandro.password", password)
    return true
  },

  logout() {
    localStorage.clear()
  }
}