import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('./views/Login.vue')
    },
    {
      path: '/app',
      name: 'app',
      component: () => import("./views/app/App.vue"),
      children: [
        {
          path: 'data',
          name: 'app-data',
          component: () => import("./views/app/data/list.vue"),
        },
        {
          path: 'data/add',
          name: 'app-data-add',
          component: () => import("./views/app/data/add.vue"),
        },
        {
          path: 'data/:id/edit',
          props: true,
          name: 'app-data-edit',
          component: () => import("./views/app/data/edit.vue"),
        },
        {
          path: 'mape',
          name: 'mape',
          component: () => import("./views/app/mape/mape.vue"),
        },
        {
          path: 'aturan',
          name: 'app-aturan',
          component: () => import("./views/app/aturan/list.vue"),
        },
        {
          path: 'aturan/add',
          name: 'app-aturan-add',
          component: () => import("./views/app/aturan/add.vue"),
        },
        {
          path: 'aturan/:id/edit',
          name: 'app-aturan-edit',
          props: true,
          component: () => import("./views/app/aturan/add.vue"),
        },
        {
          path: 'prediksi',
          name: 'app-prediksi',
          component: () => import("./views/app/prediksi/pform.vue"),
        }
      ]
    },
  ]
})
