































































import Vue from 'vue';
import HelloWorld from '@/components/HelloWorld.vue';
import auth from "@/services/auth";

export default Vue.extend({
  name: 'home',
  components: {
    HelloWorld,
  },
  data: () => ({
    buildIcons: [
      {
        icon: ["fab", "python"],
        title: "Python",
        color: "yellow"
      },
      {
        icon: ["fab", "js"],
        title: "Javascript",
        color: "orange"
      },
      {
        icon: ["fab", "vuejs"],
        title: "Vue.js",
        color: "green"
      },
    ]
  }),
  methods: {
    loginIfNecessary() {
      if (auth.checkUser()) {
        this.$router.push('/app/data')
      } else {
        this.$router.push('/login')
      }
    }
  }
});
